<template>
  <div class="massageBox">
    <transition name="currentMask">
      <div class="mask"  @click="clickMask()" v-show="messageBox" :class="sideMessage? 'w-80': ''"></div>
    </transition>
    <transition name="currentAlert">
      <div class="alert" v-show="messageBox" :style="{height: setHeight/37.5 + 'rem',marginTop:marginTop? '-60%': ''}">
        <div class="confirm" v-if="setType==='confirm'">
          <slot name="confirmColumn"></slot>
        </div>
        <div class="pormpt" v-if="setType==='pormpt'">
          <slot name="pormptColumn"></slot>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
// import { mapState, mapMutations } from 'vuex'

export default {
  data () {
    return {
      flagMessg: true, // 
      timer: null // 
    }
  },

  mounted () {
  },
  // sideMessage: messageBoxmask
  // marginTop messageBox
  props: [ 'setHeight', 'setType', 'scroll', 'marginTop', 'sideMessage' ],

  components: {
    messageBoxType () {
      return this.$store.getters.messageType
    }
  },

  computed: {
    // ...mapState([
    //   'messageBox', 'messageBoxType', 'disablemask'
    // ]),
    messageBox () {
      return this.$store.getters.messageBox
    },
    showLogout () {
      return this.$store.getters.showLogout
    }
  },

  methods: {
    // ...mapMutations([
    //   'MESSAGEBOX_SWITCH'
    // ]),

    clickMask () {
      // 300ms
      if (this.flagMessg) {
        this.flagMessg = false
        // this.$store.dispatch('hideMessage')
        this.$store.dispatch('showMessagesBox', ({type: null}))
        // 
        if (this.scroll) {
          for (let i = 0; i < this.scroll.data.length; i++) {
            if (this.messageBox) {
              this.scroll.wheels[i].enable()
            } else {
              this.scroll.wheels[i].disable()
            }
          }
        }
      }
      window.clearTimeout(this.timer)
      this.timer = window.setTimeout(() => {
        this.flagMessg = true
        window.clearTimeout(this.timer)
      }, 600)
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../styles/mixin.less';
.massageBox{
  .wh(100%, 100%);
  .fs;
  .mask{
    .fs;
    z-index: 998;
    background: rgba(0, 0, 0, .5);
  }
  .w-80{
    .wh(73.45%, 100%);
  }
  .alert{
    .wh(80%, auto);
    color: #6D6D6D;
    border-radius: 5px;
    background: #FFF;
    margin: 0 auto;
    overflow: hidden;
    margin-top: 116px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -190px;
    margin-left: -300px;
    z-index: 999;
    border-radius: 10px;
    .confirm, .pormpt{
      .wh(100%, 100%);
    }
  }
}

//
.currentMask-leave-active{
  transition: translateY 0s ease-in .3s;
}
.currentAlert-enter-active,.currentAlert-leave-active{
  transition: all .2s ease-out;
}
/*  */
.currentAlert-enter{
  transform:scale3d(.2,.2,.1);
  opacity: 0;
}
/*  */
.currentAlert-leave-active{
  transform:scale3d(.2,.2,.1);
  opacity: 0;
}
</style>



