<template>
  <div class="header">
    <!-- <div class="statusbar"></div> -->
    <div class="head-left">
      <slot name="setLeft"></slot>
      <div class="back" v-if="goBack && goBack!== 'finished'" @click.stop="back">
        <img src="../../assets/global/back@3x.png" />
      </div>
      <div class="back" v-else-if="goBack === 'finished'" @click.stop="back">
        finished
      </div>
    </div>
    <div class="head-center">{{title}}
      <slot name="system"></slot>
    </div>
    <div class="head-right">
      <slot name="setRight"></slot>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  data () {
    return {}
  },

  props: [ 'title', 'goBack' ],

  computed: {
    ...mapState(['backdre'])
  },

  methods: {
    ...mapActions(['showMessagesBox']),
    back () {
      if (this.$route.name === 'connectRoute') {
        this.showMessagesBox({type: 'exitConnect'})
      } else if (this.$route.name === 'configwifi') {
        this.$router.push({path: '/Me/addevice'})
      } else if (this.$route.name === 'addevice') {
        this.$router.push({path: '/Me'})
        this.$store.state.backdre = true
      } else if (this.$route.name === 'choose') {
        this.$router.push({path: '/Me/configwifi'})
      } else if (this.$route.name === 'rooms') {
        this.$router.push({path: '/Me'})
      } else {
        this.$router.go(-1)
        console.log(this.$route)
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../styles/mixin.less';

.header{
  .wh-l(100%, 100px + @statusBar);
  background: @themeColor;
  font-size: .48rem;
  color: #F5FAFF;
  padding-top: @statusBar;
  // .statusbar{
  //   .wh(100%, @statusBar);
  // }
  .head-left{
    .wh-f(20%, 100px);
    .back{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      display: flex;
      img{
        height: .533333rem;
        align-self: center;
      }
    }
  }
  .head-center{
    .wh-f(60%, 100px);
    text-align: center;
    line-height: 100px;
  }
  .head-right{
    .wh-f(20%, 100px);
    color: #f8b529;
  }
}

</style>



